import staffs from "@/utils/staffs.js"
import Bmob from "hydrogen-js-sdk";

import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'

let that;
let search_text;
let uid;
export default {
	components: {
		uniSegmentedControl,
		faIcon,
		loading,
		uniNavBar,
		uniIcon
	},
	data() {
		return {
			loading: true,
			shops: null,
			is_choose: false,
			items: ['已启用', '未启用'],
			current: 0,
			disabled: false
		}
	},

	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');

		if (options.type == "choose") {
			that.is_choose = true
		}
	},
	onShow() {
		uni.removeStorageSync("shop")
		that.getshop_list()
	},
	onUnload() {
		search_text = ""
	},
	methods: {
		//预览图片
		priviewImg(url){
			uni.previewImage({
				current:url,
				urls: [url],
			});
		},

		//点击选择操作列表
		choose_way(shopId) {
			uni.showActionSheet({
				itemList: ['员工列表', '查看记录'],
				success: function(res) {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');

					if (res.tapIndex == 0) {
						uni.navigateTo({
							url: "staff_in/staff_in?shopId=" + shopId
						})
					} else {
						uni.navigateTo({
							url: "record/record?shopId=" + shopId
						})
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//tab点击
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index

				if (index == 0) {
					that.disabled = false,
						that.getshop_list()
				} else if (index == 1) {
					that.disabled = true,
						that.getshop_list()
				}
			}
		},

		//选择此门店
		select_this(shop) {
			uni.setStorageSync("shop", shop)
			uni.navigateBack({
				delta: 1
			})
		},

		//编辑操作
		edit(shop) {
			uni.setStorageSync("shop", shop);
			uni.navigateTo({
				url: "add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此门店',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("shops");
			query.destroy(id).then(res => {
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				that.getshop_list()
			}).catch(err => {
				console.log(err)
			})
		},

		//前去添加员工
		goto_add() {
			let user = uni.getStorageSync("user")
			let identity = uni.getStorageSync("identity")
			if (user.is_vip || that.shops.length < 2) {
				uni.navigateTo({
					url: "add/add"
				})
			} else {
				uni.showModal({
					title: '提示',
					content: '非会员最多上传2个门店',
					confirmText: "充值会员",
					success: function(res) {
						if (res.confirm) {
							if (identity == 1) {
								uni.navigateTo({
									url: "/pages/mine/vip/vip"
								})
							} else {
								uni.showToast({
									title: "员工不能充值",
									icon: "none"
								})
							}
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			}
		},

		//输入内容筛选
		input_confirm(e) {
			search_text = e.detail.value
			that.getshop_list();
		},

		//得到门店列表
		getshop_list: function() {
			const query = Bmob.Query("shops");
			query.order("num");
			query.equalTo("parent", "==", uid);
			query.equalTo("disabled", "==", that.disabled);
			if (search_text) {
				query.equalTo("name", "==", {
					"$regex": "" + search_text + ".*"
				});

			}
			query.find().then(res => {
				console.log(res, uid)
				that.loading = false;
				let shops = res;

				uni.getStorage({
					key: 'identity',
					success: function(res) {
						if (res.data == "2") {
							staffs.get_satffAuth().then(res => {
								console.log(res)
								let manange_shops = []
								if (res.shop) {
									for (let shop of shops) {
										if (shop.objectId == res.shop.objectId) {
											manange_shops.push(shop)
										}
									}
								}
								that.shops = manange_shops;
							});
						} else if (res.data == "1") {
							that.shops = shops;
						}
					},
				})

			});
		},

	}
}
